"use strict";
import { ToggleElement, ToggleWrapper } from "components/Toggle/MultiToggle";
import { Flex } from "ui/src";
export default function RateToggle({
  currencyA,
  currencyB,
  handleRateToggle
}) {
  const tokenA = currencyA?.wrapped;
  const tokenB = currencyB?.wrapped;
  const isSorted = tokenA && tokenB && tokenA.sortsBefore(tokenB);
  return tokenA && tokenB ? <Flex width="fit-content" alignItems="center" onPress={handleRateToggle}><ToggleWrapper width="fit-content"><ToggleElement isActive={isSorted} fontSize="12px">{isSorted ? currencyA.symbol : currencyB.symbol}</ToggleElement><ToggleElement isActive={!isSorted} fontSize="12px">{isSorted ? currencyB.symbol : currencyA.symbol}</ToggleElement></ToggleWrapper></Flex> : null;
}
